import React,{useState,useEffect, useContext} from 'react';
import AuthContext from '../../context/authContext/authContext'
import WalletContext from '../../context/walletContext/walletContext'
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Copyright from '../common/Copyright'
import NavBar from '../common/Navbar'
import Paper from '@material-ui/core/Paper';
//table

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';


const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: 200,
    },
  },
  table: {
    minWidth: 650,
  },
  appBarSpacer: theme.mixins.toolbar,
  
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  }
}));

export default function Profile() {
  const classes = useStyles();
  const {getUser, user} = useContext(AuthContext)
  const {getTransactions, transactions} = useContext(WalletContext)
 
  useEffect(()=>{
    getUser()
    getTransactions()
  },[])

  let user_id = user && user._id
  return (
    <div className={classes.root}>
      <CssBaseline />
      <NavBar/>
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          <Grid container spacing={1}>
            
            {/* Chart */}
            <Paper elevation={1} className={classes.paper}>
                <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead>
                        <TableRow>
                            <TableCell>#</TableCell>
                            <TableCell>From</TableCell>
                            <TableCell>To</TableCell>
                            <TableCell align="right">Debit</TableCell>
                            <TableCell align="right">Credit</TableCell>
                        </TableRow>
                        </TableHead>
                        <TableBody>
                        {transactions && transactions.map((row,i) => (
                            <TableRow key={row._id}>
                            <TableCell component="th" scope="row">
                                {i+1}
                            </TableCell>
                            <TableCell component="th" scope="row">
                                {row.from.f_name}
                            </TableCell>
                            <TableCell component="th" scope="row">
                                {row.to.f_name}
                            </TableCell>
                            <TableCell align="right">{row.from._id === user_id ? row.to._id === user_id ? '':row.amount:''}</TableCell>
                            <TableCell align="right">{row.to._id === user_id ? row.amount:''}</TableCell>
                            </TableRow>
                        ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Paper>
          </Grid>
          
          <Box pt={4}>
            <Copyright name="Human..."/>
          </Box>
        </Container>
      </main>
    </div>
  );
}