import React,{useState,useEffect, useContext} from 'react';
import AuthContext from '../../context/authContext/authContext'
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import { useHistory } from "react-router-dom";
import Copyright from '../common/Copyright'
import NavBar from '../common/Navbar'
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MuiAlert from '@material-ui/lab/Alert';
import Snackbar from '@material-ui/core/Snackbar';
function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: 200,
    },
  },
  
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  formControl: {
    margin: theme.spacing(1),
    margin:'auto'
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
}));

export default function Profile() {
  const classes = useStyles();
  const {user, getUser, logout, errors, updateUser, userUpdated, deleteProfile} = useContext(AuthContext)
  const [state, setState] = useState({f_name:'', l_name:'', phone:'', address:'', email:''})

  useEffect(()=>{
    getUser()
    if(user){
      setState(user)
    }
    if(errors && errors !==null){setOpen(true)}
  },[errors])

  
  const {f_name, l_name, phone,address,email} = state

  const [open, setOpen] = React.useState(false);
  
  
const HandleChange = e =>{
    setState({
          ...state,
      [e.target.name] : e.target.value
    })
    
}
const formSubmit = e =>{
  e.preventDefault()
  const data = {
    f_name:state.f_name, 
    l_name:state.l_name, 
    phone:state.phone, 
    address:state.address, 
    email:state.email
  }
  updateUser(state)
  //updateUser(state)
}

const handleClose = (event, reason) => {
  if (reason === 'clickaway') {
    return;
  }

  setOpen(false);
};


  return (
    <div className={classes.root}>
      <CssBaseline />
      <NavBar/>
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          <Grid container spacing={1}>
            
            {/* Chart */}
            <Paper elevation={3} className={classes.paper}>
            <form onSubmit={formSubmit} className={classes.formControl} autoComplete="off">
              <div>
                <TextField
                  name="f_name"
                  variant="outlined"
                  required
                  fullWidth
                  id="f_name"
                  autoFocus
                  value={f_name}
                  onChange={HandleChange}
                />
                <TextField
                name="l_name"
                variant="outlined"
                required
                fullWidth
                id="l_name"
                autoFocus
                value={l_name}
                onChange={HandleChange}
              />
              </div>
              <div>
              <TextField
                name="phone"
                variant="outlined"
                required
                fullWidth
                disabled
                id="phone"
                placeholder="+91-xxxxxxxxxx"
                autoFocus
                value={phone}
                onChange={HandleChange}
              />
              <TextField
                variant="outlined"
                required
                fullWidth
                id="email"
                name="email"
                value={email}
                onChange={HandleChange}
              />
                
              </div>
              <div>
              <TextField
                name="address"
                multiline
                rows={4}
                variant="outlined"
                required
                fullWidth
                id="address"
                autoFocus
                value={address}
                onChange={HandleChange}
              />
              
              </div>
              <div>
              <Button type="submit" fullWidth variant="contained" color="primary">
                Update Profile
              </Button>
              </div>
              
            </form>
            <div>
              <Button onClick={()=>{deleteProfile()}} className={classes.root} style={{marginTop:'7px'}} fullWidth variant="contained" color="secondary">
                  Delete Profile
              </Button>
            </div>
            
            <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        {errors && errors!==null ? 
        <Alert onClose={handleClose} severity="danger">
        {errors && errors.msg}
        </Alert>
        :
        <Alert onClose={handleClose} severity="success">
            {userUpdated && userUpdated}
        </Alert>
        }
        
        </Snackbar>
            </Paper>
          </Grid>
          
          <Box pt={4}>
            <Copyright name="Human..."/>
          </Box>
        </Container>
      </main>
    </div>
  );
}