import {
    ADD_FUND_SUCCESS,
    ADD_FUND_ERROR,
    GET_TRANSACTIONS_SUCCESS,
    GET_TRANSACTIONS_ERROR,
    CLEAR_ERROR
} from '../../types'

export default (state,action) =>{
    switch(action.type){
        case ADD_FUND_SUCCESS:
            return{
                fundAdd:action.payload
            }
        case ADD_FUND_ERROR:
            return{
                errors:action.payload
            }
        case GET_TRANSACTIONS_SUCCESS:
            return{
                ...state,
                transactions:action.payload
            }
        case CLEAR_ERROR:
            return{
                fundAdd:null,
                errors:null
            }
        default :
        return state
    }

}