import {
    SUCCESS_LOGIN,
    SUCCESS_REGISTER,
    ERROR_LOGIN,
    ERROR_REGISTER,
    USER_DATA,
    ERROR_USER_DATA,
    USER_DATA_UPDATE,
    ERROR_USER_DATA_UPDATE,
    LOGOUT
} from '../../types'

export default (state,action) =>{
    switch(action.type){
        case USER_DATA:
        case USER_DATA_UPDATE:
            return{
                ...state,
                userAuth:true,
                userUpdated:action.payload,
                user:action.payload,
                errors:null
            }
        case SUCCESS_REGISTER:
        case SUCCESS_LOGIN:
            localStorage.setItem('token',action.payload.token)
            return{
                ...state,
                userAuth:true,
                errors:null
        }
        case ERROR_REGISTER:
        case ERROR_LOGIN:
        case ERROR_USER_DATA:
        case ERROR_USER_DATA_UPDATE:
        case LOGOUT:
            localStorage.removeItem('token')
            return{
                ...state,
                user:null,
                userAuth:false,
                userUpdated:null,
                errors:action.payload
            }

        default :
        return state
    }

}