import React,{useState , useContext , useEffect} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Copyright from '../common/Copyright'
import AuthContext from '../../context/authContext/authContext'
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}
const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignUp(props) {
  const classes = useStyles();
  const {registerUser , userAuth , errors} = useContext(AuthContext)
  const [state, setState] = useState({f_name:'', l_name:'', phone:'',email:'',address:'',password:''})
  const {f_name, l_name, phone,address,email,password} = state
  
const HandleChange = e =>{
    setState({
          ...state,
      [e.target.name] : e.target.value
    })
    
}
const formSubmit = e =>{
  e.preventDefault()
  registerUser(state)
}

const [open, setOpen] = React.useState(false);
  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };
useEffect(()=>{
  if(userAuth){
    props.history.push('/dashboard')
  }
  if(errors && errors !==null){setOpen(true)}
},[errors,userAuth,props.history])
  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign up
        </Typography>
        <form className={classes.form} noValidate onSubmit={formSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12} >
              <TextField
                name="f_name"
                variant="outlined"
                required
                fullWidth
                id="f_name"
                label="First Name"
                autoFocus
                value={f_name}
                onChange={HandleChange}
              />
            </Grid>
            <Grid item xs={12} >
              <TextField
                name="l_name"
                variant="outlined"
                required
                fullWidth
                id="l_name"
                label="Last Name"
                autoFocus
                value={l_name}
                onChange={HandleChange}
              />
            </Grid>
            <Grid item xs={12} >
              <TextField
                name="phone"
                variant="outlined"
                required
                fullWidth
                id="phone"
                placeholder="+91-xxxxxxxxxx"
                label="Phone Number"
                autoFocus
                value={phone}
                onChange={HandleChange}
              />
            </Grid>
            <Grid item xs={12} >
              <TextField
                name="address"
                multiline
                rows={4}
                variant="outlined"
                required
                fullWidth
                id="address"
                label="Address"
                autoFocus
                value={address}
                onChange={HandleChange}
              />
            </Grid>
           
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="email"
                label="Email Address"
                name="email"
                value={email}
                onChange={HandleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                name="password"
                label="Password"
                type="password"
                id="password"
                value={password}
                onChange={HandleChange}
              />
            </Grid>
            
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Sign Up
          </Button>
          <Grid container justify="flex-end">
            <Grid item>
              <Link href="/" variant="body2">
                Already have an account? Sign in
              </Link>
            </Grid>
          </Grid>
        </form>
        <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        {errors && errors!==null ? 
        <Alert onClose={handleClose} severity="error">
        {errors.msg ? errors.msg : errors.error[0].msg}
        </Alert>
        :''}
        </Snackbar>
      </div>
      <Box mt={5}>
        <Copyright name={"Human....."}/>
      </Box>
    </Container>
  );
}