import React,{useReducer} from 'react'
import WalletContext from './walletContext'
import WalletReducer from './walletReducer'
import setToken from '../../utils/setToken'
import axios from 'axios'

import {
    ADD_FUND_SUCCESS,
    ADD_FUND_ERROR,
    GET_TRANSACTIONS_SUCCESS,
    GET_TRANSACTIONS_ERROR,
    CLEAR_ERROR
} from '../../types'


const WalletState = (props) => {
    const initialState ={
        user:'',
        fundAdd:null,
        transactions:null,
        errors:null
    }
    const [state, dispatch] = useReducer(WalletReducer, initialState)

    //FUNCTION TO ADD FUNDS
    const addFundF = async userData => {
        const config ={
            header:{
                'Content-Type' : 'application/json'
            }
        }
        try {
            const res = await axios.post('/wallet', userData,config)
            dispatch({
                type:ADD_FUND_SUCCESS,
                payload:res.data
            })
        } catch (error) {
            dispatch({
                type:ADD_FUND_ERROR,
                payload:error.response
            })
        }
    }

    //FUNCTION TO TRANSFER FUNDS
    const transferFundsF = async userData => {
        const config ={
            header:{
                'Content-Type' : 'application/json'
            }
        }
        try {
            const res = await axios.post('/wallet/transfer', userData,config)
            dispatch({
                type:ADD_FUND_SUCCESS,
                payload:res.data
            })
        } catch (error) {
            dispatch({
                type:ADD_FUND_ERROR,
                payload:error.response.data
            })
        }
    }
    
    const getTransactions = async() => {
        try {
            const res = await axios.get('/wallet')
            dispatch({
                type:GET_TRANSACTIONS_SUCCESS,
                payload:res.data
            })
        } catch (error) {
            dispatch({
                type:GET_TRANSACTIONS_ERROR,
                payload:error
            })
        }
    }
    
    const clearError =()=>{
        dispatch({
            type:CLEAR_ERROR
        })
    }

    
    return (
       <WalletContext.Provider
       value={{
            user:state.user,
            errors:state.errors,
            transactions:state.transactions,
            fundAdd:state.fundAdd,
            addFundF,
            transferFundsF,
            getTransactions,
            clearError
            
       }}
       >{props.children}</WalletContext.Provider>
    )
}

export default WalletState
